<h1 id="顺序表与链表的比较" tabindex="-1"><a class="header-anchor" href="#顺序表与链表的比较" aria-hidden="true">#</a> 顺序表与链表的比较</h1>
<h2 id="顺序表优缺点" tabindex="-1"><a class="header-anchor" href="#顺序表优缺点" aria-hidden="true">#</a> 顺序表优缺点</h2>
<h3 id="顺序表的优点" tabindex="-1"><a class="header-anchor" href="#顺序表的优点" aria-hidden="true">#</a> 顺序表的优点</h3>
<ul>
<li>没有使用指针，不用花费额外开销</li>
<li>线性表元素的读访问非常简洁便利</li>
</ul>
<h3 id="顺序表的缺点" tabindex="-1"><a class="header-anchor" href="#顺序表的缺点" aria-hidden="true">#</a> 顺序表的缺点</h3>
<ul>
<li>预先申请固定长度的连续空间</li>
<li>插入、删除运算时间代价 O(n)</li>
</ul>
<h2 id="链表优缺点" tabindex="-1"><a class="header-anchor" href="#链表优缺点" aria-hidden="true">#</a> 链表优缺点</h2>
<h3 id="链表的优点" tabindex="-1"><a class="header-anchor" href="#链表的优点" aria-hidden="true">#</a> 链表的优点</h3>
<ul>
<li>无需事先了解线性表的长度</li>
<li>允许线性表的长度动态变化</li>
<li>能够适应经常插入删除内部元素的情况</li>
</ul>
<h3 id="链表的缺点" tabindex="-1"><a class="header-anchor" href="#链表的缺点" aria-hidden="true">#</a> 链表的缺点</h3>
<ul>
<li>存储利用指针，动态地按照需要为表中新的元素分配存储空间</li>
</ul>
<h2 id="总结" tabindex="-1"><a class="header-anchor" href="#总结" aria-hidden="true">#</a> 总结</h2>
<ul>
<li>顺序表是存储静态数据的不二选择</li>
<li>链表是存储动态变化数据的良方</li>
</ul>
<p>顺序表</p>
<ul>
<li>结点总数目大概可以估计</li>
<li>线性表中结点比较稳定（插入删除少）</li>
</ul>
<p>链表</p>
<ul>
<li>结点数目无法预知</li>
<li>线性表中结点动态变化（插入删除多）</li>
</ul>
